import React from "react";
import "./footer.css";
import logo from "../../images/infinity-graphics-logo.png";

const quickLinks01 = [
  {
    path: "#",
    display: "Marketing",
  },
  {
    path: "#",
    display: "Analytics",
  },
  {
    path: "#",
    display: "Commerce",
  },
];
const quickLinks02 = [
  {
    path: "#",
    display: "Pricing",
  },
  {
    path: "#",
    display: "Documentation",
  },
  {
    path: "#",
    display: "Guides",
  },
];
const quickLinks03 = [
  {
    path: "#about",
    display: "About",
  },
  {
    path: "#",
    display: "Job",
  },
  {
    path: "#blog",
    display: "Blog",
  },
];
const Footer = () => {
  const year = new Date().getFullYear;
  return (
    <footer className="footer">
      <div className="container">
        <div className="footer_wrapper">
          <div className="footer_logo">
            <div className="logo">
              <img src={logo} alt="" />
            </div>

            <p className="small_text description">
              {" "}
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Velit est
              sed consequuntur fugiat exercitationem praesentium optio
              asperiores molestiae! Illo, distinctio.
            </p>
          </div>

          <div className="footer_quick-links">
            <h3 className="quick_links-title">Solution</h3>
            <ul className="quick_links">
              {quickLinks01.map((item, index) => (
                <li className="quick_link-item" key={index}>
                  <a href={item.path}>{item.display}</a>
                </li>
              ))}
            </ul>
          </div>
          <div className="footer_quick-links">
            <h3 className="quick_links-title">Support</h3>
            <ul className="quick_links">
              {quickLinks02.map((item, index) => (
                <li className="quick_link-item" key={index}>
                  <a href={item.path}>{item.display}</a>
                </li>
              ))}
            </ul>
          </div>
          <div className="footer_quick-links">
            <h3 className="quick_links-title">Company</h3>
            <ul className="quick_links">
              {quickLinks03.map((item, index) => (
                <li className="quick_link-item" key={index}>
                  <a href={item.path}>{item.display}</a>
                </li>
              ))}
            </ul>
          </div>
        </div>
        <p className="copyright">
          Copyright 2023, developed by InfinityStudio All right reserved.
        </p>
      </div>
    </footer>
  );
};

export default Footer;
