import React from "react";
import "./hero.css";

import heroDarkImg from "../../images/hero-img.png";
import lightImg from "../../images/light-hero-bg.jpg";

const Hero = ({ Theme }) => {
  return (
    <section className="hero_section" id="Home">
      <div className="container">
        <div className="hero_wrapper">
          <div className="hero_content">
            <div>
              <h2>we're Creating Perfect</h2>
              <h2>Digital Product to</h2>
              <h2 className="highlight">Promote Your Brand</h2>
            </div>
            <p className="description">
              {" "}
              Lorem ipsum dolor sit amet consectetur adipisicing elit.
              Consequuntur voluptatibus, vero iusto qui rem nesciunt.
            </p>
            <div className="hero_btns">
              <button className="primary_btn">Get Started Now</button>
              <button className="secondary_btn">Discover More</button>
            </div>
          </div>
          <div className="hero_img">
            <img
              src={Theme === "light_mode" ? lightImg : heroDarkImg}
              // src={lightImg}
              alt="hero-img"
            />
          </div>
        </div>
      </div>
    </section>
  );
};

export default Hero;
