import React from "react";
import "./about.css";
import aboutImg from "../../images/about-us.jpg";

const chossData = [
  {
    icon: "ri-wifi-line",
    title: "First working process",
    desc: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Minima, similique veniam mollitia maxime fuga ex aliquid eligendi laboriosam cupiditate placeat.",
  },
  {
    icon: "ri-team-line",
    title: "Dedicated team",
    desc: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Minima, similique veniam mollitia maxime fuga ex aliquid eligendi laboriosam cupiditate placeat.",
  },
  {
    icon: "ri-time-line",
    title: "24/7 Hours support",
    desc: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Minima, similique veniam mollitia maxime fuga ex aliquid eligendi laboriosam cupiditate placeat.",
  },
];

const About = () => {
  return (
    <section id="About">
      <div className="container">
        <div className="about_wrapper">
          <div className="about_content">
            <h6 className="subtitle"> Why choose us</h6>
            <h2>Specialist in advicing client on</h2>
            <h2 className="highlight">Financial challenges</h2>
            <p className="description about_content-desc">
              Lorem ipsum dolor sit amet, consectetur adipisicing elit. Ipsa,
              illo necessitatibus odit quibusdam ipsum quos itaque porro
              distinctio pariatur nam excepturi molestias nisi quis consequuntur
              corrupti vero provident vitae eligendi.
            </p>

            <div>
              {chossData.map((item, index) => (
                <div className="choose_us-item" key={index}>
                  <span className="choose_us-icon">
                    <i className={item.icon}></i>
                  </span>
                  <div>
                    <h4 className="choose_us-title">{item.title}</h4>
                    <p className="description">{item.desc}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>

          <div className="about_img">
            <img src={aboutImg} alt="" />
          </div>
        </div>
      </div>
    </section>
  );
};

export default About;
